@import "./main.scss";

#contact {
    height: 70vh;
    background-image: url("../assets/Background-Contact-Form.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: inset 0 0 0 2000px rgba(255, 255, 255, 0.7);

    .contact-info {
        max-width: 528px;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: auto 0 auto auto;

        p.success-message {
            font-weight: 600;
            color: green;
        }

        form {
            display: flex;
            flex-direction: column;
            align-items: flex-start;

            span {
                font-size: 14px;
                margin-left: 0;
            }

            label {
                font-size: 16px;
                font-weight: 500;
                margin-bottom: 10px;
                line-height: 2rem;
            }

            button {
                align-self: flex-end;
                width: 150px;
                height: 50px;
                font-size: 18px;
                text-transform: capitalize;
                margin-right: 5px;
                margin-top: 20px;

            }

            input, textarea {
                resize: none;
                width: 100%;
                border: 0;
                background: none;
                border-bottom: 1px solid $secondary-color;
                &::placeholder {
                    opacity: 30%;
                }
            }

            .name-and-email {
                display: flex;

                .name-input, .email-input {
                    text-align: left;
                    margin: 10px;
                }
            }

            .message {
                text-align: left;
                margin: 10px;
                width: calc(100% - 20px);
     
            }

        }
    }
}